:root {
  --color_grey: #838383;
  --color__azure: #3896ab;
}

@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-v25-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/montserrat-v25-cyrillic_latin-500.ttf") format("truetype");
}
@font-face {
  font-display: swap;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-v25-cyrillic_latin-600.woff2") format("woff2"), url("../fonts/montserrat-v25-cyrillic_latin-600.ttf") format("truetype");
}
/*===== global styles =====*/
* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1480px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  font-family: inherit;
  border: none;
  appearance: none;
  outline: none;
  background-color: transparent;
}

button {
  cursor: pointer;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.header {
  min-height: 46px;
  background-color: #000;
  position: relative;
  z-index: 10;
}

.header__container {
  position: relative;
  max-width: 1440px;
  min-width: 320px;
  margin: 0 auto;
}

.header__menu {
  position: relative;
  padding: 0 20px;
}
.header__menu ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0 20px;
  padding: 0 86px;
  overflow-x: auto;
}
.header__menu li {
  flex-shrink: 0;
  color: var(--color_grey, #838383);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
}
.header__menu a {
  display: inline-block;
  padding: 14px 0;
  transition: color 0.2s ease-out;
}
.header__menu a:hover {
  color: white;
}
@media (max-width: 1300px) {
  .header__menu {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 0;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
  }
  .header__menu ul {
    display: block;
    padding: 13px 69px;
    background-color: #000;
  }
  .header__menu li:not(:last-child) {
    margin-bottom: 28px;
  }
  .header__menu a {
    padding: 0;
  }
}

.header__btn {
  position: relative;
  display: none;
  justify-content: flex-start;
  align-items: center;
  gap: 0 17px;
  padding: 12px 20px 11px 27px;
  color: var(--color_grey, #838383);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: -0.16px;
  cursor: pointer;
}
.header__btn,
.header__btn svg,
.header__btn path {
  transition: all 0.2s ease-out;
}
.header__btn:hover {
  color: white;
}
.header__btn:hover path {
  fill: white;
}
.active .header__btn svg {
  transform: scaleY(-1);
}
@media (max-width: 1300px) {
  .header__btn {
    display: flex;
  }
}

.main {
  flex-grow: 1;
}

.panel__container {
  width: 100%;
  max-width: 1920px;
  min-height: 71px;
  margin: 0 auto;
  padding: 0 60px 0 54px;
  display: grid;
  grid-template-columns: 1fr 138px 1fr;
  align-items: center;
}
@media (max-width: 1300px) {
  .panel__container {
    padding: 0 20px;
  }
}
.panel__container button,
.panel__container input,
.panel__container input::placeholder {
  color: #000;
  font-size: 16px;
  font-weight: 600;
}
.panel__container button,
.panel__container a {
  transition: all 0.2s ease-out;
}
.panel__container button:hover,
.panel__container a:hover {
  color: var(--color_grey, #838383);
}

.panel__nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0 38px;
}

.panel__showCategories,
.panel__search {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0 16px;
}

.panel__showCategories svg:nth-child(2) {
  display: none;
}
.panel__showCategories.active svg:nth-child(1) {
  display: none;
}
.panel__showCategories.active svg:nth-child(2) {
  display: block;
}

.panel__search {
  position: relative;
  width: 26vw;
  height: 54px;
}
.panel__search img {
  position: absolute;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
}
.panel__search input {
  display: block;
  width: 100%;
  height: 100%;
  padding: 0 48px;
}
.panel__search input:focus, .panel__search input.active {
  background-color: #f1f1f1;
}

.panel__clear {
  display: none;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
  cursor: pointer;
}
.panel__clear.visible {
  display: block;
}

.panel__menu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.panel__user {
  display: flex;
  align-items: center;
  margin-right: 44px;
}
.panel__user:hover .user__icon {
  border-color: var(--color_grey, #838383);
}
.panel__user .panel__nonAuth {
  display: none;
}
.panel__user .panel__auth {
  display: flex;
}
.panel__user.not-authorized .panel__nonAuth {
  display: flex;
}
.panel__user.not-authorized .panel__auth {
  display: none;
}

.panel__nonAuth,
.panel__auth {
  align-items: center;
  gap: 0 16px;
}

.user__icon {
  width: 28px;
  height: 28px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #d9d9d9;
  border: 2px solid #000;
  border-radius: 50%;
  text-transform: uppercase;
  transition: all 0.2s ease-out;
}

.panel__favorites {
  margin-right: 36px;
}

.panel__favorites,
.panel__cart {
  position: relative;
}

.panel__showCategories path,
.panel__user path,
.panel__cart path,
.panel__favorites path {
  transition: all 0.2s ease-out;
}
.panel__showCategories:hover path,
.panel__user:hover path,
.panel__cart:hover path,
.panel__favorites:hover path {
  fill: var(--color_grey, #838383);
}

.panel__amout {
  display: none;
  position: absolute;
  bottom: 15px;
  left: 15px;
  z-index: 2;
  min-width: 24px;
  min-height: 24px;
  align-items: center;
  justify-content: center;
  padding: 3px;
  font-size: 14px;
  font-weight: 600;
  border-radius: 50%;
  color: white;
  background-color: var(--color__azure, #3896ab);
}
.panel__amout::after {
  content: "";
  display: block;
  padding-top: 100%;
}
.panel__amout span {
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
}
.non-empty .panel__amout {
  display: flex;
}

footer {
  padding: 20px;
}